'use client'
import { Container } from "react-bootstrap";
import { motion } from "framer-motion";
import LogoImage from "../assets/Images/logo.png";
import styled from "styled-components";

const Section = styled.section`
  width: 100%;
  height: 30vh;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: row; /* Ensure items are in a row */
  align-items: center; /* Center items vertically */
  flex-wrap: wrap; /* Allow wrapping on smaller screens */
  background-color: var(--dark);
  color: #ffffff;

  @media screen and (max-width: 48em) {
    flex-direction: column; /* Stack items vertically on smaller screens */
    text-align: center;
  }

  @media screen and (max-width: 850px) {
    flex-direction: column; /* Ensure items are in a row */
    height: 100vh;
  }

  & > div {
    flex: 1; /* Each child takes equal space */
    margin: 1rem;

    @media screen and (max-width: 48em) {
      margin: 0.5rem 0;
    }
  }
`;

const BlockRight = styled.div`
    @media screen and (max-width: 850px) {
      margin: 0.5rem 0;
    }
`;

const FooterSection = () => {
  return (
    <footer className="w-full">
      <Container>
        <Section className="pt-5 pb-4">
          <motion.div
            initial={{ x: -200 }}
            whileInView={{ x: 0 }}
            transition={{ duration: 0.8 }}
          >
            <img
              src={LogoImage}
              alt="logo"
              className="mb-3"
            />
            <p>
              Please contact us if you have any specific <br /> idea or request.
            </p>
          </motion.div>
          <motion.div
            initial={{ y: -200 }}
            whileInView={{ y: 0 }}
            transition={{ duration: 0.8 }}
          >
            <div>
              <h6 className="text-uppercase mb-2 fw-normal">
                Company
              </h6>
              <ul className="p-0">
                <li>How it works</li>
                <li>Market</li>
                <li>About Us</li>
              </ul>
            </div>
          </motion.div>
          <motion.div
            initial={{ x: 200 }}
            whileInView={{ x: 0 }}
            transition={{ duration: 0.8 }}
          >
            <BlockRight>
              <h6 className="text-uppercase mb-2 fw-normal">
                Socials
              </h6>
              <ul className="p-0">
                <li>Twitter</li>
                <li>Instagram</li>
                <li>Facebook</li>
              </ul>
            </BlockRight>
          </motion.div>
        </Section>
        <motion.div
          initial={{ y: -200 }}
          whileInView={{ y: 0 }}
          transition={{ duration: 0.8 }}
          style={{ textAlign: 'center', color: 'gray' }} /* Center content and set text color to gray */
        >
          <p> 2024 TRIBU.</p>
          <p>
            All rights reserved
          </p>
        </motion.div>
      </Container>
    </footer>
  );
}

export default FooterSection;
